.ShopItem {
  display: flex;
  padding: 15px;
}

.ShopItem img {
  width: 35%;
}

.ShopItem__Description {
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-left: 50px;
}

.ShopItem__Description button {
  border: none;
  padding: 8px;
  color: #fff;
  background-color: #1c1e3f;
  cursor: pointer;
  font-size: 18px;
}

.ShopItem__Description button:hover {
  opacity: 0.7;
}
