.CartItem {
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-right: 20px;
}

.CartItem__Quantity {
    display: flex;
}

.CartItem__Quantity p {
    padding: 0px 5px;
}

.CartItem button {
  width: 20px;
  height: 20px;
  border: none;
  color: #fff;
  background-color: #1c1e3f;
  cursor: pointer;
  font-size: 18px;
  border-radius: 50%
}
